.btn,
.button{
  background-color: transparent;
  border-radius: $global-radius;
  border: 1px solid $border-color;
  color: $color-secondary;
  cursor: pointer;
  display: inline-block;
  font-size: inherit;
  font-weight: 100;
  height: 38px;
  letter-spacing: .1rem;
  line-height: 38px;
  margin-bottom: 1rem;
  padding: 0 30px;
  text-align: center;
  text-decoration: none;
  transition-duration: 200ms;
  white-space: nowrap;
  &:focus,
  &:hover {
    @include hover-style;
  }

  &:focus{
    outline: .1rem dotted $gray;
  }

  &:active{
    background-color: $color-primary;
  }

  &_primary {
    color: $white;
    background-color: $color-primary;
    border-color: $color-primary;

    &:focus,
    &:hover {
      color: $white;
      background-color: darken($color-primary, 10%);
      border-color: $color-primary;
    }
  }
}