html {
  box-sizing: border-box;
  font-size: 62.5%;
  height: 100%; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  line-height: 2.4rem;
  font-weight: 400;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  color: #222; }

a {
  color: #496DDB;
  text-decoration: none;
  word-wrap: break-word; }
  a:hover {
    color: shade(#496DDB, 15%); }

ul {
  list-style: circle inside; }

ol {
  list-style: decimal inside;
  padding-left: 0;
  margin-top: 0; }

ul {
  padding-left: 0;
  margin-top: 0; }
  ul ul, ul ol {
    margin: 1.5rem 0 1.5rem 3rem;
    font-size: 90%; }

ol ol, ol ul {
  margin: 1.5rem 0 1.5rem 3rem;
  font-size: 90%; }

li {
  margin-bottom: 1rem; }

input {
  appearance: none;
  background-color: #fff;
  border-radius: 0.2rem;
  border: 1px solid #d1d1d1;
  box-shadow: none;
  box-sizing: border-box;
  height: 38px;
  padding: 6px 10px;
  -moz-appearance: none;
  -webkit-appearance: none; }
  input:focus {
    border: 1px solid #3498db;
    outline: 0; }

pre {
  overflow: auto; }

code {
  background: #e3e3e3;
  border-radius: 0.2rem;
  border: 1px solid #d3d3d3;
  font-size: 90%;
  margin: 0 .2rem;
  padding: .2rem .5rem;
  white-space: nowrap; }

pre > code {
  display: block;
  padding: 1rem 1.5rem;
  white-space: pre; }

.error-text {
  color: #C3423F; }

button, input {
  margin-bottom: 1rem; }

form,
ol,
p,
pre,
ul {
  margin-bottom: 2.4rem; }

li {
  margin-bottom: 1rem; }

label {
  margin-bottom: .25rem; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 2rem;
  font-weight: 300;
  word-wrap: break-word; }

h1 {
  font-size: 4.0rem;
  line-height: 1.2;
  letter-spacing: -.1rem; }

h2 {
  font-size: 3.6rem;
  line-height: 1.25;
  letter-spacing: -.1rem; }

h3 {
  font-size: 3.0rem;
  line-height: 1.3;
  letter-spacing: -.1rem; }

p {
  margin-top: 0; }

.header {
  margin: 20px 0; }

.container {
  min-width: 280px;
  width: 80%;
  max-width: 1200px;
  margin: 0 auto; }

.btn,
.button {
  background-color: transparent;
  border-radius: 0.2rem;
  border: 1px solid #bbb;
  color: #7e7e7e;
  cursor: pointer;
  display: inline-block;
  font-size: inherit;
  font-weight: 100;
  height: 38px;
  letter-spacing: .1rem;
  line-height: 38px;
  margin-bottom: 1rem;
  padding: 0 30px;
  text-align: center;
  text-decoration: none;
  transition-duration: 200ms;
  white-space: nowrap; }
  .btn:focus, .btn:hover,
  .button:focus,
  .button:hover {
    color: #7e7e7e;
    border-color: lightgray;
    outline: 0; }
  .btn:focus,
  .button:focus {
    outline: 0.1rem dotted #a8a8a8; }
  .btn:active,
  .button:active {
    background-color: #3498db; }
  .btn_primary,
  .button_primary {
    color: #fff;
    background-color: #3498db;
    border-color: #3498db; }
    .btn_primary:focus, .btn_primary:hover,
    .button_primary:focus,
    .button_primary:hover {
      color: #fff;
      background-color: #217dbb;
      border-color: #3498db; }

.list_group {
  list-style-type: none;
  border-bottom-color: #a8a8a8; }
  .list_group .list__item {
    position: relative;
    border: 1px solid #d3d3d3;
    padding: 8px 10px; }

.list_dom {
  list-style-type: none;
  padding: 0 0 0 1rem; }
  .list_dom .list__item {
    position: relative;
    font-size: 1.5rem; }
    .list_dom .list__item:first-child:before, .list_dom .list__item:not(:last-child):after {
      position: absolute;
      border-color: #d3d3d3;
      top: 0;
      left: -1rem;
      width: 0.8rem;
      border-style: none none solid solid;
      content: '';
      border-width: 0.1rem; }
    .list_dom .list__item:first-child:before {
      height: 1rem; }
    .list_dom .list__item:not(:last-child):after {
      bottom: -2.2rem; }

.terminal__input {
  display: block;
  width: 100%;
  background-color: #272727;
  color: #fff;
  padding-left: 10px; }

.link {
  cursor: pointer;
  color: #496DDB; }
